import React from 'react';
import { useMetrics } from '@/context/MetricsContext';
import Layout from '@/components/layout/Layout';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Button } from '@/components/ui/button';
import {
  LineChart,
  Line,
  BarChart,
  Bar,
  PieChart,
  Pie,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  Cell,
  RadarChart,
  Radar,
  PolarGrid,
  PolarAngleAxis,
  PolarRadiusAxis,
  Area,
  AreaChart,
  ComposedChart
} from 'recharts';

export default function Analytics() {
  const {
    metrics,
    qualityData,
    npsData,
    customerServiceData,
    maintenanceSatisfaction,
    currentPeriod,
    setCurrentPeriod
  } = useMetrics();

  //   NPS 
  const calculateNPSMetrics = () => {
    const currentData = currentPeriod === 'weekly' ? npsData : npsData;
    const averages = {
      newCustomers: 0,
      afterFirstYear: 0,
      longTerm: 0
    };
    
    currentData.forEach(data => {
      averages.newCustomers += data.newCustomers;
      averages.afterFirstYear += data.afterFirstYear;
      averages.longTerm += data.longTerm;
    });

    const count = currentData.length;
    return {
      newCustomers: (averages.newCustomers / count).toFixed(1),
      afterFirstYear: (averages.afterFirstYear / count).toFixed(1),
      longTerm: (averages.longTerm / count).toFixed(1)
    };
  };

  //  NPS 
  const npsChartData = npsData.map(data => ({
    period: data.week,
    ' ': data.newCustomers,
    '  ': data.afterFirstYear,
    ' ': data.longTerm,
    '': (data.newCustomers + data.afterFirstYear + data.longTerm) / 3
  }));

  const qualityRadarData = qualityData.map(data => ({
    metric: data.week,
    ' ': data.facilityManagement,
    '': data.maintenance,
    '': data.delivery
  }));

  //   
  const serviceCallsData = [
    { name: '', value: customerServiceData.calls.complaints },
    { name: ' ', value: customerServiceData.calls.contactRequests },
    { name: ' ', value: customerServiceData.calls.maintenanceRequests },
    { name: '', value: customerServiceData.calls.inquiries },
    { name: ' ', value: customerServiceData.calls.officeInterested },
    { name: ' ', value: customerServiceData.calls.projectsInterested }
  ];

  const totalCalls = serviceCallsData.reduce((sum, item) => sum + item.value, 0);
  const servicePerformanceData = [
    {
      name: '',
      total: totalCalls,
      resolved: customerServiceData.maintenance.resolved,
      pending: customerServiceData.maintenance.inProgress,
      cancelled: customerServiceData.maintenance.cancelled
    }
  ];

  //   
  const satisfactionData = [
    { name: ' ', value: maintenanceSatisfaction.serviceQuality.veryHappy },
    { name: '', value: maintenanceSatisfaction.serviceQuality.happy },
    { name: '', value: maintenanceSatisfaction.serviceQuality.neutral },
    { name: ' ', value: maintenanceSatisfaction.serviceQuality.unhappy },
    { name: '  ', value: maintenanceSatisfaction.serviceQuality.veryUnhappy }
  ];

  const satisfactionTrends = [
    { name: ' ', ...maintenanceSatisfaction.serviceQuality },
    { name: ' ', ...maintenanceSatisfaction.closureTime },
    { name: '   ', ...maintenanceSatisfaction.firstTimeResolution }
  ];

  const COLORS = {
    positive: ['#4CAF50', '#8BC34A', '#CDDC39'],
    neutral: ['#FFC107'],
    negative: ['#FF9800', '#F44336']
  };

  const npsAverages = calculateNPSMetrics();

  return (
    <Layout>
      <div className="container mx-auto p-6">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-3xl font-bold">  </h1>
          <div className="flex gap-2">
            <Button
              variant={currentPeriod === 'weekly' ? 'default' : 'outline'}
              onClick={() => setCurrentPeriod('weekly')}
            >
              
            </Button>
            <Button
              variant={currentPeriod === 'yearly' ? 'default' : 'outline'}
              onClick={() => setCurrentPeriod('yearly')}
            >
              
            </Button>
          </div>
        </div>
        
        <Tabs defaultValue="nps" className="space-y-4">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="nps"> NPS </TabsTrigger>
            <TabsTrigger value="service">  </TabsTrigger>
            <TabsTrigger value="satisfaction">  </TabsTrigger>
          </TabsList>

          <TabsContent value="nps">
            <div className="grid gap-4">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <Card>
                  <CardHeader>
                    <CardTitle> NPS -  </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-3xl font-bold text-green-600">
                      {npsAverages.newCustomers}%
                    </div>
                  </CardContent>
                </Card>
                <Card>
                  <CardHeader>
                    <CardTitle> NPS -  </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-3xl font-bold text-blue-600">
                      {npsAverages.afterFirstYear}%
                    </div>
                  </CardContent>
                </Card>
                <Card>
                  <CardHeader>
                    <CardTitle> NPS -  </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-3xl font-bold text-purple-600">
                      {npsAverages.longTerm}%
                    </div>
                  </CardContent>
                </Card>
              </div>

              <Card>
                <CardHeader>
                  <CardTitle>  NPS - {currentPeriod === 'weekly' ? '' : ''}</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="h-[400px]">
                    <ResponsiveContainer width="100%" height="100%">
                      <ComposedChart data={npsChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="period" />
                        <YAxis />
                        <Tooltip />
                        <Legend />
                        <Area type="monotone" dataKey="" fill="#8884d8" stroke="#8884d8" />
                        <Line type="monotone" dataKey=" " stroke="#4CAF50" strokeWidth={2} />
                        <Line type="monotone" dataKey="  " stroke="#2196F3" strokeWidth={2} />
                        <Line type="monotone" dataKey=" " stroke="#9C27B0" strokeWidth={2} />
                      </ComposedChart>
                    </ResponsiveContainer>
                  </div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>   - {currentPeriod === 'weekly' ? '' : ''}</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="h-[400px]">
                    <ResponsiveContainer width="100%" height="100%">
                      <RadarChart data={qualityRadarData}>
                        <PolarGrid />
                        <PolarAngleAxis dataKey="metric" />
                        <PolarRadiusAxis />
                        <Radar name=" " dataKey=" " stroke="#8884d8" fill="#8884d8" fillOpacity={0.6} />
                        <Radar name="" dataKey="" stroke="#82ca9d" fill="#82ca9d" fillOpacity={0.6} />
                        <Radar name="" dataKey="" stroke="#ffc658" fill="#ffc658" fillOpacity={0.6} />
                        <Legend />
                      </RadarChart>
                    </ResponsiveContainer>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="service">
            <div className="grid gap-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Card>
                  <CardHeader>
                    <CardTitle>   </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="h-[400px]">
                      <ResponsiveContainer width="100%" height="100%">
                        <PieChart>
                          <Pie
                            data={serviceCallsData}
                            cx="50%"
                            cy="50%"
                            innerRadius={60}
                            outerRadius={120}
                            paddingAngle={5}
                            dataKey="value"
                          >
                            {serviceCallsData.map((entry, index) => (
                              <Cell 
                                key={`cell-${index}`} 
                                fill={COLORS.positive[index % COLORS.positive.length]} 
                              />
                            ))}
                          </Pie>
                          <Tooltip />
                          <Legend />
                        </PieChart>
                      </ResponsiveContainer>
                    </div>
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader>
                    <CardTitle>  </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="h-[400px]">
                      <ResponsiveContainer width="100%" height="100%">
                        <ComposedChart data={servicePerformanceData}>
                          <CartesianGrid strokeDasharray="3 3" />
                          <XAxis dataKey="name" />
                          <YAxis />
                          <Tooltip />
                          <Legend />
                          <Bar dataKey="total" stackId="a" fill="#8884d8" name=" " />
                          <Bar dataKey="resolved" stackId="b" fill="#82ca9d" name=" " />
                          <Bar dataKey="pending" stackId="b" fill="#ffc658" name=" " />
                          <Bar dataKey="cancelled" stackId="b" fill="#ff7300" name="" />
                          <Line type="monotone" dataKey="resolved" stroke="#82ca9d" name=" " />
                        </ComposedChart>
                      </ResponsiveContainer>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </div>
          </TabsContent>

          <TabsContent value="satisfaction">
            <div className="grid gap-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Card>
                  <CardHeader>
                    <CardTitle>  </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="h-[400px]">
                      <ResponsiveContainer width="100%" height="100%">
                        <PieChart>
                          <Pie
                            data={satisfactionData}
                            cx="50%"
                            cy="50%"
                            innerRadius={60}
                            outerRadius={120}
                            paddingAngle={5}
                            dataKey="value"
                            label={({ name, percent }) => `${name} (${(percent * 100).toFixed(0)}%)`}
                          >
                            {satisfactionData.map((entry, index) => (
                              <Cell 
                                key={`cell-${index}`}
                                fill={
                                  index < 2 
                                    ? COLORS.positive[index]
                                    : index === 2 
                                      ? COLORS.neutral[0]
                                      : COLORS.negative[index - 3]
                                }
                              />
                            ))}
                          </Pie>
                          <Tooltip />
                          <Legend />
                        </PieChart>
                      </ResponsiveContainer>
                    </div>
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader>
                    <CardTitle>  </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="h-[400px]">
                      <ResponsiveContainer width="100%" height="100%">
                        <RadarChart data={satisfactionTrends}>
                          <PolarGrid />
                          <PolarAngleAxis dataKey="name" />
                          <PolarRadiusAxis />
                          <Radar name=" " dataKey="veryHappy" stroke="#4CAF50" fill="#4CAF50" fillOpacity={0.6} />
                          <Radar name="" dataKey="happy" stroke="#8BC34A" fill="#8BC34A" fillOpacity={0.6} />
                          <Radar name="" dataKey="neutral" stroke="#FFC107" fill="#FFC107" fillOpacity={0.6} />
                          <Legend />
                        </RadarChart>
                      </ResponsiveContainer>
                    </div>
                  </CardContent>
                </Card>
              </div>

              <Card>
                <CardHeader>
                  <CardTitle>  </CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4 max-h-[400px] overflow-y-auto">
                    {maintenanceSatisfaction.comments.map((comment, index) => (
                      <div key={index} className="p-4 border rounded-lg bg-card">
                        <p className="text-sm mb-2">{comment.text}</p>
                        <div className="text-xs text-muted-foreground">
                          {comment.username} - {comment.date} {comment.time}
                        </div>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </Layout>
  );
} 