import { useState, useEffect } from "react";
import { useAuth } from "@/context/AuthContext";
import Layout from "@/components/layout/Layout";
import { useNotification } from "@/context/NotificationContext";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Edit2, Trash2, UserPlus } from "lucide-react";

export default function Settings() {
  const { addNotification } = useNotification();
  const { users, addUser, deleteUser, resetUserPassword } = useAuth();
  
  //   
  const [newUser, setNewUser] = useState<{
    username: string;
    role: string;
    password: string;
  }>({
    username: "",
    role: "",
    password: ""
  });

  //     
  const handleNewUserChange = (field: string, value: string) => {
    setNewUser((prev) => ({
      ...prev,
      [field]: value
    }));
  };

  //   
  const handleAddUser = () => {
    if (!newUser.username || !newUser.role || !newUser.password) {
      addNotification({
        title: "",
        message: "    ",
        type: "warning"
      });
      return;
    }
    
    //        
    if (users.some(user => user.username === newUser.username)) {
      addNotification({
        title: "",
        message: "       ",
        type: "error"
      });
      return;
    }
    
    addUser(newUser);
    
    addNotification({
      title: " ",
      message: `   ${newUser.username} `,
      type: "success"
    });
    
    console.log("   :", newUser.username);
    
    //        
    const savedUsers = localStorage.getItem('auth_users');
    if (savedUsers) {
      console.log('   localStorage  :', JSON.parse(savedUsers));
    }
    
    //     
    setNewUser({
      username: "",
      role: "",
      password: ""
    });
  };

  //  
  const handleDeleteUser = (id: string) => {
    const userToDelete = users.find(user => user.id === id);
    if (!userToDelete) return;
    
    deleteUser(id);
    
    addNotification({
      title: " ",
      message: `   ${userToDelete.username} `,
      type: "success"
    });
  };

  //    
  const handleResetPassword = (id: string) => {
    const userToReset = users.find(user => user.id === id);
    if (!userToReset) return;
    
    //     
    const newPassword = prompt("   :", "");
    
    if (newPassword) {
      resetUserPassword(id, newPassword);
      
      addNotification({
        title: " ",
        message: `      ${userToReset.username} `,
        type: "success"
      });
    }
  };

  //         
  useEffect(() => {
    console.log('    :', users);
  }, [users]);

  return (
    <Layout>
      <div className="space-y-6">
        <h1 className="text-2xl font-bold"> </h1>
        
        <Card>
          <CardHeader>
            <CardTitle> </CardTitle>
            <CardDescription>    </CardDescription>
          </CardHeader>
          <CardContent>
            <h3 className="text-lg font-semibold mb-4"> </h3>
            
            <div className="rounded-md border mb-8">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead className="text-right"> </TableHead>
                    <TableHead className="text-right"></TableHead>
                    <TableHead className="text-right"> </TableHead>
                    <TableHead className="text-right"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {users.map((user) => (
                    <TableRow key={user.id}>
                      <TableCell>{user.username}</TableCell>
                      <TableCell>{user.role}</TableCell>
                      <TableCell></TableCell>
                      <TableCell>
                        <div className="flex items-center gap-2">
                          <Button
                            variant="outline"
                            size="icon"
                            onClick={() => handleResetPassword(user.id)}
                            title="  "
                          >
                            <Edit2 className="h-4 w-4 text-blue-500" />
                          </Button>
                          <Button
                            variant="outline"
                            size="icon"
                            onClick={() => handleDeleteUser(user.id)}
                            title=" "
                          >
                            <Trash2 className="h-4 w-4 text-red-500" />
                          </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
            
            <div>
              <h3 className="text-lg font-semibold mb-4">  </h3>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
                <div className="space-y-2">
                  <Label htmlFor="username"> </Label>
                  <Input
                    id="username"
                    placeholder="  "
                    value={newUser.username}
                    onChange={(e) => handleNewUserChange("username", e.target.value)}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="role"></Label>
                  <Select
                    value={newUser.role}
                    onValueChange={(value) => handleNewUserChange("role", value)}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder=" " />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value=" "> </SelectItem>
                      <SelectItem value="   ">   </SelectItem>
                      <SelectItem value="   ">   </SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="password"> </Label>
                  <Input
                    id="password"
                    type="password"
                    placeholder="  "
                    value={newUser.password}
                    onChange={(e) => handleNewUserChange("password", e.target.value)}
                  />
                </div>
              </div>
              
              <Button
                onClick={handleAddUser}
                className="flex items-center"
              >
                <UserPlus className="ml-2 h-4 w-4" />
                 
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </Layout>
  );
}
